body {
  border-top: solid 3px #00baff;
}

.l-container {
  width: 100%;
}

.l-inner {
  width: 960px;
  margin-right: auto;
  margin-left: auto;
}

.l-nav {
  position: relative;
  width: 128px;
  min-height: 301px;
}

.l-side {
  width: 300px;
}

.l-main {
  width: 468px;
  margin: 0 32px;
}

.p-header {
  width: 100%;
  padding-top: 2px;
  border-top: solid 3px #ffe600;
}

.p-header__inner {
  position: relative;
  height: 55px;
}

.p-header__siteLogo {
  position: absolute;
  top: 8px;
  left: 0;
  width: 148px;
  height: 38px;
}

.p-header__siteDescription {
  position: absolute;
  top: 18px;
  left: 160px;
  color: #666;
  font-size: 7.5px;
  font-size: 0.75rem;
}

.p-header__snsButton {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 15px;
  right: 280px;
  width: 80px;
}

.p-header__snsButton--facebook {
  display: block;
}

.p-header__snsButton--facebook:before {
  display: block;
  content: '';
  width: 24px;
  height: 24px;
  background-image: url(/common/images/sprite.png);
  background-position: -34px -91px;
  background-size: 200px 189px;
}

.p-header__snsButton--twitter {
  display: block;
}

.p-header__snsButton--twitter:before {
  display: block;
  content: '';
  width: 24px;
  height: 24px;
  background-image: url(/common/images/sprite.png);
  background-position: -59px -91px;
  background-size: 200px 189px;
}

.p-header__snsButton--rss {
  display: block;
}

.p-header__snsButton--rss:before {
  display: block;
  content: '';
  width: 24px;
  height: 24px;
  background-image: url(/common/images/sprite.png);
  background-position: -84px -91px;
  background-size: 200px 189px;
}

.p-header__search {
  position: absolute;
  top: 12px;
  right: 0;
  width: 266px;
  height: 30px;
}

.p-header__search--query {
  display: block;
  width: 236px;
  height: 30px;
  padding: 0 10px;
  background: #f8f8f8;
  font-size: 7.5px;
  font-size: 0.75rem;
  line-height: 30px;
  border: solid 1px #bbb;
  border-right: none;
  border-radius: 15px 0 0 15px;
}

.p-header__search--submit {
  display: block;
  width: 30px;
  height: 30px;
  background: #999;
  border: none;
  border-radius: 0 15px 15px 0;
  -webkit-appearance: none;
  cursor: pointer;
  z-index: 0;
}

.p-header__search--submit:after {
  display: block;
  content: '';
  width: 16px;
  height: 16px;
  margin-left: 5px;
  background-image: url(/common/images/sprite.png);
  background-position: -159px -152px;
  background-size: 200px 189px;
}

.p-pageInfo {
  padding: 8px 0;
  background: #f8f8f8;
  border-top: solid 1px #ddd;
  border-bottom: solid 1px #ddd;
}

.p-pageInfo__breadCrumb {
  display: flex;
  justify-content: flex-start;
}

.p-pageInfo__breadCrumb--item {
  margin-right: 8px;
}

.p-pageInfo__breadCrumb--item:after {
  display: inline-block;
  content: '>';
  width: 10px;
  height: 10px;
  font-size: 8px;
  font-size: 0.8rem;
}

.p-pageInfo__breadCrumb--item:last-child:after {
  content: '';
}

.p-pageInfo__breadCrumb--link {
  font-size: 8px;
  font-size: 0.8rem;
  text-decoration: underline;
}

.p-pageInfo__login {
  display: flex;
  justify-content: flex-start;
}

.p-pageInfo__login--item {
  font-size: 8px;
  font-size: 0.8rem;
}

.p-pageInfo__login--item:first-child:after {
  display: inline-block;
  content: '|';
  max-width: 4px;
  margin: 0 6px 0 2px;
  color: #CCC;
}

.p-pageInfo__login--link {
  color: #00aeee;
}

.p-importantNotice {
  text-align: center;
}

.p-importantNotice__link {
  color: #ff0036;
  font-size: 8px;
  font-size: 0.8rem;
  text-decoration: underline;
}

.p-pickupL {
  display: flex;
}

.p-pickupL__list {
  width: 320px;
  border-right: solid 1px #fff;
}

.p-pickupL__link {
  display: block;
  position: relative;
  width: 100%;
  height: 286px;
  transition: .4s;
}

.p-pickupL__link:hover {
  opacity: .8;
}

.p-pickupL__link--txt {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 16px 10px;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  font-size: 10px;
  font-size: 1rem;
  font-weight: bold;
  line-height: 1.5rem;
}

.p-pickupS {
  width: 320px;
  height: 286px;
}

.p-pickupS__list {
  width: 100%;
  margin-bottom: 1px;
}

.p-pickupS__link {
  display: block;
  position: relative;
  height: 142px;
  transition: .4s;
}

.p-pickupS__link:hover {
  opacity: .8;
}

.p-pickupS__link:last-child {
  margin-bottom: 0;
}

.p-pickupS__link--img {
  display: block;
  width: 160px;
  height: 142px;
}

.p-pickupS__link--txt {
  display: block;
  width: 160px;
  height: 142px;
  padding: 10px;
  background: #666;
  color: #fff;
  font-size: 8px;
  font-size: 0.8rem;
}

.p-globalNav__link {
  display: block;
  width: 100%;
  padding: 12px 10px;
  color: #333;
}

.p-globalNav__link:hover {
  color: #00aeee;
}

.c-heading {
  padding: 0 8px 4px;
  font-size: 9px;
  font-size: 0.9rem;
  border-bottom: solid 1px #333;
}

.c-heading__label {
  color: #666;
  font-size: 8px;
  font-size: 0.8rem;
}

.c-article__list {
  border-bottom: solid 1px #ccc;
}

.c-article__link {
  display: block;
  padding: 12px 8px;
  transition: .4s;
}

.c-article__link:hover {
  background: rgba(246, 227, 206, 0.1);
}

.c-article__img {
  width: 24.5%;
  margin-right: 1.5%;
}

.c-article__details {
  width: 74%;
}

.c-article__title {
  margin-bottom: 8px;
  color: #333;
  font-size: 9px;
  font-size: 0.9rem;
}

.c-article__description {
  margin-bottom: 8px;
  color: #999;
}

.c-article__viewCount, .c-article__favCount, .c-article__curatorName {
  margin-right: 10px;
  padding-right: 10px;
  color: #999;
  border-right: solid 1px #999;
}

.c-article__curatorName {
  border-right: none;
}

.p-footerNav {
  width: 100%;
  padding: 20px 0;
  border-top: solid 1px #ccc;
  border-bottom: solid 1px #ffe600;
}

.p-footerNav__heading {
  margin-bottom: 2px;
  color: #666;
  font-weight: bold;
}

.p-footerNav__heading:nth-of-type(1) {
  font-size: 10px;
  font-size: 1rem;
}

.p-footerNav__category {
  margin-bottom: 20px;
}

.p-footerNav__list {
  display: flex;
  justify-content: flex-start;
}

.p-footerNav__list__item {
  margin-right: 6px;
  padding-right: 6px;
  border-right: solid 1px #ccc;
}

.p-footerNav__list__link {
  color: #666;
  font-size: 9px;
  font-size: 0.9rem;
}

.p-footerNav__list__link:hover {
  color: #00aeee;
}

.p-relationSite {
  margin-bottom: 20px;
}

.p-relationSite__list {
  display: inline-block;
  padding-right: 4px;
  border-right: solid 1px #ccc;
}

.p-relationSite__list__link {
  color: #999;
  font-size: 7px;
  font-size: 0.7rem;
  letter-spacing: -.04rem;
}

.p-relationSite__list__link:hover {
  color: #00aeee;
}

.p-footer {
  padding: 20px 0;
}

.p-footerLogo {
  width: 259px;
  height: 27px;
}

.footerLicense__logo {
  display: block;
  content: '';
  width: 80px;
  height: 35px;
  margin: 0 auto 0 10px;
  background-image: url(/common/images/sprite.png);
  background-position: 0px 0px;
  background-size: 200px 189px;
}

#test {
  position: relative;
}
